;(function(win){
    /**
    ** 
    **  es6  
    **/
    win.fireEvent = function(ta,type,data,bub){
		ta = ta || document;
		var evt = new Event(type,{bubbles:bub||true});
		evt.data = data || {name:"unknow"};
		ta.dispatchEvent(evt);
    }
	
}(window));
define("utils", function(){});

